@import "@/styles/_variables.sass"















































































































































































































































.content-categories-list
  display: flex
  flex-flow: column
  justify-content: start
  align-items: stretch

  &>.v-drag-and-drop
    flex: 0 0 auto
    overflow: hidden
    padding-left: $step*2

    .item
      display: flex
      flex-flow: row nowrap
      justify-content: stretch
      align-items: center
      background-color: $color-white

      &>*
        flex: 0 0 auto
        padding: $step*0.5 0

      &>.item-padding
        padding-left: $step*3

      &>button
        font-size: $step*3
        color: $color-dark

        &>.disabled
          color: alpha($color-dark, '88')

      &>div
        flex: 1 1 auto
        padding-right: $step

        &>h5
          color: $color-dark

  .v-drag-and-drop .item.active, .v-drag-and-drop .item:hover
    background-color: $color-gray

  .v-drag-and-drop .item.hidden
    display: none
