@import "@/styles/_variables.sass"


































































$section-min-width: 360px
$sidebar-max-width: 768px
$bookmark-width: $section-min-width - $step

//-----------------------------------------------------------------------------
// Main frame
#index-page
  flex: 1 1 $desktop-min-width
  position: relative
  width: 100%
  min-height: 100vh
  display: flex
  flex-flow: row nowrap
  background: top/cover url("~@/assets/Background.svg")

#index-page-main
  flex: 1 0 $section-min-width
  padding-bottom: 20vh

#index-page-sidebar
  flex: 0 0 30%
  min-width: $section-min-width
  max-width: $sidebar-max-width
  border-left: $step solid $color-primary
  background: bottom/80% auto url("~@/assets/Ink_background.svg") no-repeat, $color-white


//-----------------------------------------------------------------------------
// Bookmark
.bookmark
  width: $bookmark-width
  margin: 0 auto

//-----------------------------------------------------------------------------
// Sidebar
#index-page-sidebar
  #index-page-spacer
    height: 2rem

  .authorization
    width: 85%
    margin: 0 auto

//-----------------------------------------------------------------------------
// Main
#index-page-main
  .bookmark
    #index-page-logo
      margin: 2rem 0 0
      width: $bookmark-width * 0.5
      height: $bookmark-width * 0.5 / 1.6

    p
      margin: 2rem 0 4rem

    button
      display: block
      width: 70%
      height: $step*5
      margin: 1rem auto 0
      border-radius: $step
      border: 1px solid $color-white

      &.active
        background-color: $color-white
        color: $color-primary

      @media (hover: hover), (-ms-high-contrast:none)
        &:hover, &:focus
          background-color: $color-dark !important
          color: $color-white !important

//-----------------------------------------------------------------------------
// Footer
#index-page-footer
  position: absolute
  left: $step*2
  bottom: $step*2
  color: $color-dark
