@import "@/styles/_variables.sass"






























































































































.content-notes-list
  display: flex
  flex-flow: column
  justify-content: start
  align-items: stretch

  &>.item
    flex: 0 0 auto
    overflow: hidden
    padding-left: $step*2

    &>div
      display: flex
      flex-flow: column
      padding: $step*2 $step $step 0

      &>* + *
        margin-top: $step*0.5

      &>h5
        color: $color-dark

      &>.categories
        display: flex
        flex-flow: row wrap
        align-items: center
        justify-content: start

        &>button
          color: $color-tertiary

        &>button + button
          margin-left: $step

        &>button:hover
          text-decoration: underline

      &>p
        max-height: 2.4rem
        overflow: hidden

        // Keep this part as an example
        // &:after
        //   content: ""
        //   text-align: right
        //   position: absolute
        //   top: 1.2rem
        //   right: 0
        //   width: 70%
        //   height: 1.2rem
        //   background: linear-gradient(to right, alpha($color-white, "00"), alpha($color-white, "CC") 50%)

  &>.item + .item>div
    border-top: 1px solid alpha($color-primary, "88")

  &>.item.active, &>.item:hover
    background-color: $color-gray
