@import "@/styles/_variables.sass"






































































































.v-text-editor
  position: relative
  overflow: hidden
  -webkit-text-size-adjust: none

  width: 100%
  min-height: 100%

  &>*
    width: 100%
    overflow: hidden
    font: inherit
    letter-spacing: inherit
    white-space: pre-wrap
    word-wrap: break-word
    vertical-align: top

    &.hidden
      visibility: hidden

  &>textarea, &>input, &>span
    position: absolute
    top: 0
    left: 0
    height: 100%
    z-index: 2
    display: block
    resize: none
    background-color: transparent
    color: inherit

  &>span
    z-index: 1
    color: darken($color-gray, 30)

  &>div
    pointer-events: none
    color: transparent
