@import "@/styles/_variables.sass"
































































































































//-----------------------------------------------------------------------------
// Main frame
#view-desktop
  min-width: $desktop-min-width
  width: 100%
  height: 100vh
  position: relative
  display: flex
  flex-flow: row
  align-items: stretch

#view-desktop-navigation
  flex: 0 0 auto
  overflow-x: hidden
  overflow-y: auto
  display: flex
  flex-flow: column
  padding: $step*2
  background-color: $color-primary
  color: $color-white

  &>*
    flex: 0 0 auto
    padding: $step*2 0

  &>* + *
    border-top: 1px solid $color-white

  &>.logo
    padding-top: 0
    white-space: nowrap

    &>*
      display: inline-block

    object
      vertical-align: bottom
      width: 3rem
      margin-right: $step

  &>*.active
    color: $color-primary
    background-color: $color-white

  &>button:hover
    color: $color-white
    background-color: $color-dark

//-----------------------------------------------------------------------------
// Settings
#view-desktop-settings
  position: absolute
  top: 0
  right: 0
  height: 100%
  width: 50vw
  z-index: 100
  border-left: $step solid $color-primary
  background-color: $color-white

#view-desktop-dialogs
  position: absolute
  top: 0
  right: 0
  height: 100%
  width: 100%
  z-index: 200
  overflow: auto
  display: flex
  flex-flow: column
  align-items: center
  justify-content: center
  background-color: alpha($color-primary, '66')

//-----------------------------------------------------------------------------
// Settings customization
#view-desktop-settings
  &.hidden
    display: none

//-----------------------------------------------------------------------------
// Dialogs customization
#view-desktop-dialogs
  &.hidden
    display: none
