@import "@/styles/_variables.sass"




















































































































































































.content-filters-list
  display: flex
  flex-flow: column
  justify-content: start
  align-items: stretch

  &>.group, &>.item
    flex: 0 0 auto
    overflow: hidden
    padding-left: $step*2

    &:hover
      background-color: $color-gray

  &>.group>div
    display: flex
    flex-flow: row nowrap
    align-items: center
    justify-content: start
    margin-top: $step*2
    color: $color-dark
    border-bottom: 1px solid alpha($color-dark, '88')

    &>span
      margin-left: auto
      font-size: $step*4 - $step
      line-height: $step*4 - $step

      &.hidden
        display: none

  &>.item>div
    margin: $step*0.5 0 0 $step*0.5

  &>.item.active>div
    font-weight: bold

  &>.item.hidden
    display: none
