@import "@/styles/_variables.sass"








































































































.v-category-input
  position: relative
  color: $color-tertiary

  &>div
    overflow: hidden
    display: flex
    flex-flow: column
    justify-content: stretch
    align-items: stretch

    &>div
      border-radius: $step*2
      border: 1px solid $color-tertiary

    &>div:nth-of-type(1)
      overflow: hidden
      display: flex
      flex-flow: row
      justify-content: stretch
      align-items: center

      &>input
        display: none
        padding: 0 $step

        &.active
          display: block

      &>button
        font-size: $step*2
        line-height: $step*2
        padding: 1px 0 0 1px

        &:hover
          color: $color-white
          background-color: $color-tertiary

    &>div:nth-of-type(2)
      position: absolute
      top: 120%
      left: 0
      width: 100%
      z-index: 1000
      overflow: hidden
      display: none
      flex-flow: column
      justify-content: stretch
      align-items: stretch
      background-color: $color-white

      &.active
        display: flex

      &>button
        padding: $step*0.5 $step
        text-align: start

        &:hover
          background-color: $color-gray

      &>p
        padding: $step*0.5 $step
        font-weight: bold
        text-align: center
