@import "@/styles/_variables.sass"



































































































































































































































































.authorization
  &>h2
    margin: 4rem 0 2rem
    text-align: center

  &>label
    margin-top: 1rem
    border-radius: $step
    box-shadow: 0 0 0 1px $color-primary
    display: flex
    flex-flow: row
    align-items: stretch
    justify-content: stretch
    background-color: $color-white
    overflow: hidden

    input
      flex: 1 1 auto
      padding: $step $step*6 $step $step
      color: $color-dark

    button
      flex: 0 0 $step*3
      padding: 0 $step
      margin-left: -$step*5

      span
        font-size: 1.5rem
        vertical-align: middle

      @media (hover: hover), (-ms-high-contrast:none)
        &:hover
          background-color: $color-primary
          color: $color-white

    &.error
      &>*
        color: $color-error
        border-color: $color-error

      @media (hover: hover), (-ms-high-contrast:none)
        button:hover
          background-color: $color-error
          color: $color-white

  &>button
    height: $step*5
    width: 100%
    margin-top: 2rem
    border-radius: $step
    background-color: $color-primary
    color: $color-white

    @media (hover: hover), (-ms-high-contrast:none)
      &:hover, &:focus
        background-color: $color-dark

  &>ul
    width: 100%
    padding: $step*1.5
    margin-top: 1rem
    list-style: inside
    color: $color-dark

    &>li + li
      margin-top: $step

    &>li.error
      color: $color-error
