@import "@/styles/_variables.sass"

















.icon-button
  color: $color-primary
  width: $step*4
  height: $step*4
  border-radius: $step
  padding: $step*0.5
  font-size: $step*4 - $step
  line-height: $step*4 - $step

  &.active
    background-color: $color-primary
    color: $color-white

  &:hover
    background-color: $color-dark
    color: $color-white
