@import "@/styles/_variables.sass"
























// There are several fixes for compatibility with IE:
// 1. Svg tag must have preserveAspectRatio="xMidYMin slice" attribute
// 2. Svg tag must have a container
// 3. Svg tag must have 1% height, 100% width, visible overflow,
//    margin-bottom set to some % value

.bookmark
  width: 100%
  display: flex
  flex-flow: column

  &>div:nth-of-type(1)
    flex: 1 0 auto
    overflow: hidden
    padding: $step*2
    background-color: $color-primary
    color: $color-white
    text-align: center

  &>div:nth-of-type(2)
    width: 100%
    flex: 0 0 auto
    margin-top: -1px
    svg
      height: 1%
      width: 100%
      overflow: visible
      margin-bottom: 5%
      fill: $color-primary
