@import "@/styles/_variables.sass"























































































.content-category-editor
  display: flex
  flex-flow: column
  align-items: stretch
  justify-content: stretch

  &>.header
    flex: 0 0 auto
    display: flex
    flex-flow: row wrap
    padding: 0 $step*0.5 $step*0.5 0
    border-bottom: 1px solid $color-primary

    &>*
      flex: 0 0 auto
      overflow: hidden
      margin: $step*0.5 0 0 $step*0.5

  &>.content
    flex: 1 1 auto
    overflow-x: hidden
    overflow-y: auto
    padding: $step

    &>* + *
      margin-top: $step*0.5

    &>.category
      display: flex
      flex-flow: row wrap
      align-items: center
      justify-content: start

      &>button
        color: $color-tertiary

      &>button + button
        margin-left: $step

      &>button:hover
        text-decoration: underline

    &>.title
      font-weight: bold
      font-size: $step*3
      letter-spacing: 1px
      color: $color-dark
      min-height: $step*3*1.2

    &>.text
      font-size: $step*2
      color: $color-primary
      min-height: 70vh
